import { ThemeProvider } from 'styled-components';

const themeConfig = {
  color: {
    primaryColor: '#293250',
    secondaryColor: '#FFD55A',
    backgroundWhite: '#f7f7f7'
  },
  subFamily: 'Carter One'
}

const Theme = ({ children }) => {
  return (
    <ThemeProvider theme={themeConfig}>
      {children}
    </ThemeProvider>
  );
};

export default Theme;
